import { API_KEY } from './constants';
import DriveApiV3 from './driveApiV3';

class PickerWidget {

  async init(callback, imgSettings) {
    console.log('class');
    console.log('initPicker');
    await PickerWidget.handlePickerLoad();
    console.log(this.isPickFile)
    await PickerWidget.createPicker(callback, imgSettings, );
    console.log('picker created');
  }

  static handlePickerLoad() {
    return new Promise((res, rej) => {
      gapi.load('picker', { callback: res, onerror: rej });
    });
  }

  static async createPicker(callback, imgSettings, isPickFile) {
    console.log(callback, imgSettings);
    const oauthToken = gapi.auth.getToken().access_token;

    const myDriveView = new google.picker.DocsView(google.picker.ViewId.FOLDERS)
      .setLabel('My Drive')
      .setOwnedByMe(true)
      .setIncludeFolders(true)
      .setSelectFolderEnabled(true);

    const sharedDriveView = new google.picker.DocsView(google.picker.ViewId.FOLDERS)
      .setEnableDrives(true)
      .setIncludeFolders(true)
      .setSelectFolderEnabled(true);

    const sharedWithMeView = new google.picker.DocsView(google.picker.ViewId.FOLDERS)
      .setLabel('Shared with Me')
      .setOwnedByMe(false)
      .setIncludeFolders(true)
      .setSelectFolderEnabled(true);

    const starredView = new google.picker.DocsView(google.picker.ViewId.FOLDERS)
      .setLabel('Starred')
      .setStarred(true)
      .setIncludeFolders(true)
      .setSelectFolderEnabled(true);

    const picker = new google.picker.PickerBuilder()
      .addView(myDriveView)
      .addView(sharedDriveView)
      .addView(sharedWithMeView)
      .addView(starredView)
      .setOAuthToken(oauthToken)
      .setDeveloperKey(API_KEY)
      .setCallback(d => PickerWidget.pickerCallback(d, callback, imgSettings))
      .enableFeature(google.picker.Feature.SUPPORT_DRIVES)
      .setTitle('Pick a folder')
      .build();
    picker.setVisible(true);
  }

  static async pickerCallback(data, cb, imgSettings) {
    console.log(data)
    // if (data[google.picker.Response.ACTION] === google.picker.Action.PICKED) {
    //   const image = await DriveApiV3.getImageById(data.docs[0].id);
    //   console.log(image)
    // }

    // console.log(data, cb, imgSettings);
    if (data[google.picker.Response.ACTION] === google.picker.Action.PICKED) {
      const pickedObj = data[google.picker.Response.DOCUMENTS][0];
      console.log(pickedObj)
      console.log(pickedObj.id);
      // cb(imgSettings, [pickedObj.id]);
    }
  }
}

export const PickFile = new PickerWidget();
console.log(PickFile)
export const PickFolder = new PickerWidget();
