import { useState } from "react";
import { Gapi, handlDriveUploadForPicker, handleDriveDownloadForPicker } from "../gDrive";
import { testUpload } from '../gDrive/helpers';

const Button = () => {
    const [scopes, setScopes] = useState([]);

    console.log(Gapi);

    const handlePickFileClick = () => {
        handleDriveDownloadForPicker();
    }

    const handlePickFolderClick = () => {
        const imgSettings = {};
        handlDriveUploadForPicker(imgSettings);
    }

    const handleShareDriveClick = () => {
        Gapi.shareDrive()
    }
    
    const handleAuthClick = () => {
        Gapi.authUser();
    }
    
    const handleUploadClick = () => {
        testUpload()
    }

    const handleSignInClick = () => {
        Gapi.signInWithGoogle()
    }

    const handleSignOutClick = () => {
        Gapi.signOutGoogle()
    }

    const handleRevokeAccessClick = () => {
        Gapi.revokeAccess()
    }

    const handleGrantedScopesClick = async () => {
        const scopes = await Gapi.getGrantedScopes();
        setScopes(scopes.split(' ').filter(val => val.includes('auth')))
    };

    // const handleUploadDriveClick = () => {

    // };

    return (
        <>
            <div>
                <button onClick={handleShareDriveClick}>Share Drive</button>
                <button onClick={handleAuthClick}>Authorize</button>
                <button onClick={handleUploadClick}>upload</button>
                <button onClick={handleSignInClick}>signIn</button>
                <button onClick={handleSignOutClick}>signOut</button>
                <button onClick={handleRevokeAccessClick}>revoke access</button>
                <button onClick={handlePickFileClick}>pick file</button>
                <button onClick={handlePickFolderClick}>pick folder</button>
                <button onClick={handleGrantedScopesClick}>see granted scopes</button>
                {/* <button onClick={handleUploadDriveClick}>upload Drive</button> */}
            </div>
            {scopes.map(scope => <p key={scope}>{scope}</p>)}
            <div>
                <canvas id="myCanvas"></canvas>
            </div>
            {/* <img id="driveImg" src="" alt="img"/> */}
        </>
    )
}

export default Button
